* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: Arial, sans-serif;
    background-color: #f5f5f5;
    /*padding: 24px;*/
}

.main {
    display: flex;
    flex-direction: column;
    gap: 24px;
}







#status {
    overflow: auto;

    background-color: #fff;
    height: 120px;
    padding: 10px 12px;

    border: 1px solid #ccc;
    border-radius: 8px;

    font-size: 14px;
    line-height: 1.6;
}

.videoSectionWrap {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
}

.actionRow.switchRow {
    width: 100%;

    justify-content: center;
}
.switchRow {
    flex-direction: column;
}
.switchRow > label {
    width: 100%;

    display: flex;
    justify-content: center;
}

.switchRow > label input {
    flex: 1;
    max-width: 500px;
}

.videoSectionWrap .videoWrap {
    display: flex;
    justify-content: center;
    align-items: center;

    /*background: linear-gradient(0deg, rgba(0, 0, 0, 0.02) 0%, rgba(0, 0, 0, 0.02) 100%),*/
    /*  radial-gradient(*/
    /*    108.09% 141.42% at 0% 100%,*/
    /*    rgba(219, 255, 213, 0.3) 0%,*/
    /*    rgba(255, 255, 255, 0) 100%*/
    /*  ),*/
    /*  linear-gradient(135deg, #ffeede 5.71%, #ffd9d9 47%, #a3dce7 93.47%);*/
}

.videoWrap .videoEle {
    width: 100%;
    max-height: 400px;
}

/*---------- Switch START ----------*/
.switchWrap {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: '';
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

input:checked + .slider {
    background-color: #2196f3;
}

input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}
/*---------- Switch END ----------*/

.videoSectionWrap .hide {
    display: none;
}

.videoSectionWrap .show {
    display: flex;
}

.hide {
    display: none;
}
.show {
    display: flex;
}


.svgAnimation{
    top:20%;
    left: 18%;
    position: absolute;
    width: 700px;
    /*height: 400px;*/
}


@media (max-width: 600px) {
    .svgAnimation{
        left: 5%;
        position: relative;
        width: 250px;
        height: 250px;
    }
}